"use client"

import Image from "next/image"

export function DojoCoreBall() {
  return (
    <div className="relative h-[28rem] w-[28rem] max-w-[90vw] flex items-center justify-center">
      {/* Glowing background effect */}
      <div className="absolute inset-0 rounded-full blur-[80px] bg-gradient-to-br from-cyan-200 via-indigo-500 to-pink-500 opacity-50 animate-pulse" />

      {/* Logo container */}
      <div className="relative z-10 w-[70%] h-[70%] flex items-center justify-center">
        <Image
          src="/Xemployeelogo.svg"
          alt="Xemployee Logo"
          width={400}
          height={400}
          className="w-full h-full object-contain drop-shadow-[0_0_60px_rgba(255,255,255,0.5)] animate-pulse"
          priority
        />
      </div>
    </div>
  )
}

